<template>
    <li id="notification-container">
        <ul v-for="notif in notifications" :key="notif" 
            v-bind:style="{
                marginBottom: notif.intro == true ? '16px' : '0px',
                padding: notif.intro == true ? '12px' : '0px',
                height: notif.intro == true ? '48px' : '0px',
                opacity: notif.intro == true && notif.outro == false ? '1' : '0'
            }"
            class="notification">
            
            <img src="../assets/information.png">
            <p>{{ notif.message }}</p>
            <svg class="notif-close" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 256 256" style="enable-background:new 0 0 256 256;">
                <path d="M128,0C57.3,0,0,57.3,0,128s57.3,128,128,128s128-57.3,128-128S198.7,0,128,0z M178.9,167.6l-11.3,11.3
                    L128,139.3l-39.6,39.6l-11.3-11.3l39.6-39.6L77.1,88.4l11.3-11.3l39.6,39.6l39.6-39.6l11.3,11.3L139.3,128L178.9,167.6z"/>
            </svg>
        </ul>
    </li>
</template>

<script>
var globalNotifList = null;

export default {
    name: 'NotificationList',
    
    created() {
        if(globalNotifList != null) {
            console.error('Two instances of ' + this.name);
            return;
        }
        
        globalNotifList = this;
        
        this.addNotif('info', 'Added item to favorites');
        this.addNotif('info', 'Removed item from favorites');
    },
    
    data() {
        return {
            notifications: []
        }
    },
    
    methods: {
        addNotif(type, message) {
            let notif = {
                type,
                message,
                
                intro: false,
                outro: false
            };
            
            this.notifications.push(notif);
            
            /* The animation */
            setTimeout(function() {
                notif.intro = true;
            }, 500);
            
            /* Showing the notification for 5 seconds */
            setTimeout(function() {
                notif.outro = true;
                
                setTimeout(function() {
                    const index = this.notifications.indexOf(notif);
                    this.notifications.splice(index, 1);
                }.bind(this), 500);
            }.bind(this), 4500);
        }
    }
}
</script>

<style scoped>

#notification-container
{
    position: fixed;
    
    right: 32px;
    bottom: 32px;
    
    display: flex;
    flex-direction: column;
    justify-items: center;
    
    z-index: 999;
}

.notification
{    
    padding: 12px;
    
    width: 350px;
    height: 0px;
    
    margin-bottom: 16px;
    
    border-radius: 8px;
    
    background-color: white;
    box-shadow: 0 8px 20px var(--shadow-color);
    
    display: flex;
    
    transition: all .5s ease-in-out;
}

.notification > img
{
    height: 100%;
}

.notification > p
{
    margin-left: 12px;
    flex: 1 1 auto;
}

.notif-close
{    
    cursor: pointer;
}

.notif-close > path
{
    fill: rgba(83, 83, 83, 0.548);
}

</style>