<template>
    <img src="../assets/bkg2.jpg" id="image-display" ref="imageDisplay">
        <div id="image-scroll" @click="scrollDown">
            
        </div>
    <img>
</template>

<script>
export default {
    methods: {
        scrollDown() {
            const docRect = document.body.getBoundingClientRect();
            const rect = this.$refs.imageDisplay.getBoundingClientRect();
            window.scrollTo({ top: rect.bottom - docRect.top + 70, left: 0, behavior: 'smooth' });
        }
    }
}
</script>

<style scoped>

#image-display
{
    position: relative;
    
    width: 100%;
    height: calc(100vh - 70px);
    object-fit: cover;
    
    user-select: none;
    pointer-events: none;
    
    margin-bottom: 48px;
}

#image-scroll
{
    width: 96px;
    height: 96px;
    
    position: absolute;
    
    bottom: 24px;
    
    border-radius: 100%;
    border: 4px white solid;
    box-shadow: 0 8px 20px rgba(49, 49, 49, 0.637);
    
    cursor: pointer;
    
    transition: transform .2s ease-in-out;
}

#image-scroll:hover
{
    transform: scale(120%);
}

</style>