</template>

<script>
export default {
    name: 'NavBar',
    
    created() {
        /* For hiding and showing the navigation bar */
        window.addEventListener('scroll', this.onscroll);
    },
    
    unmounted() {
        window.removeEventListener('scroll', this.onscroll);
    },
    
    data() {
        return {
            lastPos: 0,
            barPos: 0,
        }
    },
    
    methods: {
        onscroll() {
            if(window.scrollY <= 70) {
                this.barPos = 0;
                return;
            }
            
            const scrollAmount = window.scrollY - this.lastPos;
            this.barPos -= scrollAmount;
            
            this.barPos = Math.min(0, Math.max(-70, this.barPos));
            
            this.lastPos = window.scrollY;
        },
        
        mouseeneter() {
            
        },
        
        mouseleave() {
            
        }
    }
}
</script>

