<template>
    <div class="product-display" @click="render">
        <img class="display-img" src="../../assets/8ball-bag.png">
        <img ref="displayImg" class="fallback-img" src="../../assets/fallback.png">
        <div class="desc-container">
            <h1>349,00 kr</h1>
            
            <svg class="star" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 256 256" @click="favourite">
                <circle cx="128" cy="128" r="0">
                    <animate class="fill-circle"  begin="indefinite" attributeName="r" values="0;128" dur=".2s" fill="freeze"/>
                    <animate class="clear-circle" begin="indefinite" attributeName="r" values="128;0" dur=".2s" fill="freeze"/>
                </circle>
                <path d="M0,0V256H256V0ZM202.73,243,128,203.71,53.27,243l14.27-83.21L7.08,100.85,90.63,88.71,128,13l37.37,75.71,83.55,12.14-60.46,58.94Z"/>
                <polygon points="128 13 165.37 88.71 248.92 100.85 188.46 159.78 202.73 243 128 203.71 53.27 243 67.54 159.78 7.08 100.85 90.63 88.71 128 13"/>
            </svg>
            
            <p>
                <strong>{{ title }}</strong>
                {{ /*' · '*/ ' — ' + description }}
            </p>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ProductDisplay',
    
    created() {
        
    },
    
    props: {
        title: String,
        description : String
    },
    
    data() {
        return {
            fav: false
        }
    },
    
    methods: {
        render(e) {
            const target = e.target || e.srcElement;
            target;
            this.$refs.displayImg.style.opacity = 0;
            
            console.log('yä');
        },
        
        favourite(e) {
            const target = e.target || e.srcElement;
            const fill = target.getElementsByClassName('fill-circle')[0];
            const clear = target.getElementsByClassName('clear-circle')[0];
            
            this.fav = !this.fav;
            
            console.log(this.fav);
            
            if(this.fav) {
                fill.beginElement();
            } else {
                clear.beginElement();
            }
        }
    }
}
</script>

<style scoped>

.product-display
{
    position: relative;
    
    width: 100%;
    
    cursor: pointer;
}

.product-display > *
{
    position: relative;
    width: 100%;
}

.product-display > .fallback-img
{
    position: relative;
    
    transition: opacity .2s ease-in;
}

.product-display > .display-img
{
    position: absolute;
    left: 0;
    top: 0;
}

.desc-container
{    
    margin-top: 8px;
    
    height: auto;
}

.desc-container > h1
{
    font-size: 12pt;
}

.desc-container > p
{   
    margin-top: 8px;
    
    
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* number of lines to show */
    -webkit-box-orient: vertical;
}

/* The favourite button */
.desc-container > .star
{
    position: absolute;
    right: 0;
    top: 0;
    
    width: 24px;
    
    cursor: pointer;
}

.desc-container > .star > *
{
    pointer-events: none;
}

.desc-container > .star > path
{
    fill: var(--background-color);
}

.desc-container > .star > polygon
{
    fill: none;
    stroke:#000;
    stroke-linejoin: round;
    stroke-width: 14px;
}

.desc-container > .star > circle
{ 
    fill: #f2ce05;
}


</style>